export function getYoutubeEmbedUrl(trailerYoutubeId, trailerUrl) {
    if (trailerYoutubeId) {
        return "https://www.youtube.com/embed/".concat(trailerYoutubeId);
    }
    if (!trailerUrl)
        return null;
    try {
        var url = new URL(trailerUrl);
        if (url.hostname.includes('youtube.com')) {
            var id = url.searchParams.get('v');
            if (id)
                return "https://www.youtube.com/embed/".concat(id);
        }
        if (url.hostname === 'youtu.be') {
            var _id = url.pathname.replace('/', '');
            if (_id)
                return "https://www.youtube.com/embed/".concat(_id);
        }
    }
    catch (e) {
        console.error('Некорректный trailerUrl:', trailerUrl, e);
    }
    return null;
}
